.main-header {
  background: $black-pearl-to-down;
  position: sticky;
  top: 0;
  z-index: 999;

  &__container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 35px;
    min-height: 120px;
  }

  @include vp-1023 {
    &__container {
      padding-top: 20px;
      padding-bottom: 25px;
      min-height: 100px;
    }
  }

  @include vp-767 {
    &__container {
      padding-top: 10px;
      padding-bottom: 20px;
      min-height: 70px;
    }
  }

  @include vp-1023 {
    position: static;
  }
}

.main-header__logo-link {
  display: flex;
  flex-shrink: 0;
  transition: $trans-default;
  margin-right: 5px;

  &--mobile {
    display: none;
  }

  &:hover,
  &:active {
    opacity: 0.7;
  }

  @include vp-767 {
    align-self: flex-end;
    margin-right: 26px;

    &--mobile {
      display: flex;
    }

    &--desktop {
      display: none;
    }
  }
}

.main-header__list {
  display: grid;
  min-width: 480px;
  grid-template-columns: 225px 225px;
  gap: 6%;
  margin-left: 20px;

  @include vp-1023 {
    grid-template-columns: 1fr 1fr;
    min-width: auto;
    align-self: flex-start;
    margin-left: auto;
    padding: 0;
    column-gap: 0;
  }

  @include vp-767 {
    column-gap: 10px;
  }
}

.main-header__item {
  margin: 14px 0 15px;

  @include vp-1023 {
    margin: 0;
    margin-right: 20px;
  }

  @include vp-767 {
    margin-right: 10px;
  }
}

.main-header__link {
  color: $color-alice-blue;
  position: relative;
  padding-left: 35px;
  transition: $trans-default;

  &:hover,
  &:active {
    opacity: 0.7;
  }

  @include vp-1023 {
    display: block;
    padding: 20px;
  }
}

.main-header__link span {
  width: 195px;

  @include vp-1023 {
    display: none;
  }
}

.main-header__link svg {
  position: absolute;
  top: auto;
  left: 0;

  @include vp-1023 {
    top: 10px;
    left: 10px;
  }
}

.main-header__btn-wrapper {
  max-width: 250px;
  flex-shrink: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin-right: 0;
  margin-left: 15px;

  @include vp-1023 {
    display: none;
  }
}

.main-header__btn {
  padding-left: 15px;
  padding-right: 17px;
  margin-top: 5px;
  margin-bottom: 12px;
  margin-right: 60px;
  border: 1px solid $color-alice-blue;
  line-height: 18px;
}
